.nav-wrapper {
  background-color: var(--header-bg-color);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  min-height: 4rem;
  width: 100%;
  margin: 0 auto;
  z-index: 100;
  padding: 0;
  display: block;
  transform-style: preserve-3d;
  backface-visibility: hidden;
  -webkit-transform-style: preserve-3d;
  -webkit-backface-visibility: hidden;
}
.nav-list a {
  text-decoration: none !important;
}
.nav-wrapper.nav-box-sd {
  box-shadow: 0 1px 3px 0 rgba(0, 34, 77, 0.05);
}
.nav-wrapper > .nav {
  max-width: 80rem;
  width: 90%;
  height: 4rem;
  margin: 0 auto;
  padding: 0 0.5rem;
  display: flex;
  justify-content: space-between;
}
.nav-wrapper .collapse-nav {
  position: absolute;
  top: 15px;
  left: 40px;
  z-index: 999;
}
.slideIn {
  animation: slideIn 0.6s ease 1;
  /*调用动画：动画名、时间、时间线条、播放次数*/
  -webkit-animation: slideIn 0.6s ease 1;
  /*调用动画：动画名、时间、时间线条、播放次数*/
}
.slideOut {
  animation: slideOut 0.6s ease-out 1;
  /*调用动画：动画名、时间、时间线条、播放次数*/
  -webkit-animation: slideOut 0.6s ease-out 1;
  /*调用动画：动画名、时间、时间线条、播放次数*/
}
@-webkit-keyframes slideIn {
  from {
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
    opacity: 0;
  }
  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}
@keyframes slideIn {
  from {
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
    opacity: 0;
  }
  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}
@-webkit-keyframes slideOut {
  from {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
  to {
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
    opacity: 0;
  }
}
@keyframes slideOut {
  from {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
  to {
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
    opacity: 0;
  }
}
.nav-wrapper > .nav > .toggle-nav {
  z-index: 2;
  transition: all 0.3s;
  -webkit-transition: all 0.3s;
}
@media all and (min-width: 800px) {
  .nav-wrapper > .nav > .toggle-nav {
    display: none;
  }
}
.nav-wrapper.-sticky {
  position: fixed;
}
.nav-wrapper.-sticky.-bottom {
  top: unset;
  bottom: 0;
}
.nav-wrapper.-sticky.-bottom > .nav > .nav-list {
  bottom: 4rem;
  top: 0;
  transform: translateY(calc(4rem - 100%));
}
.nav-wrapper.-sticky.-bottom > .nav > .nav-list > .list.-left {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.nav-wrapper .search {
  line-height: 58px;
  font-size: 1rem;
  position: relative;
  right: 40px;
}
.nav-wrapper .search .icon-search {
  font-size: 20px;
  cursor: pointer;
}
.nav-wrapper .search .icon-search:hover {
  color: var(--base-hl-color);
}
.nav-bg-fff {
  background-color: var(--header-bg-color) !important;
  transition: all 0.6s;
  -webkit-box-shadow: 0 1px 3px 0 rgba(0, 34, 77, 0.05);
  box-shadow: 0 1px 3px 0 rgba(0, 34, 77, 0.05);
}
.nav-bg-fff .nav-list > .list > .item > .link,
.nav-bg-fff .nav-list > .list > .item > .void-link {
  color: var(--header-link-color) !important;
}
@media screen and (min-width: 800px) {
  .nav-wrapper.-sticky.-bottom > .nav > .nav-list > .list.-left {
    flex-direction: row;
    justify-content: flex-start;
  }
  .nav-wrapper {
    background: none;
  }
  .nav-list > .list > .item > .link,
  .nav-list > .list > .item > .void-link {
    color: #ffffff !important;
  }
}
@media screen and (min-width: 800px) {
  .nav-wrapper.-sticky.-bottom {
    top: unset;
    bottom: unset;
  }
}
.toggle-nav {
  background-color: transparent;
  cursor: pointer;
  box-shadow: none;
  border: 0;
  outline: none;
  margin: 0;
  padding: 0.5rem;
}
.logo-link {
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5rem;
}
.logo-link > .logo {
  width: 100%;
  height: auto;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  -webkit-backface-visibility: hidden;
  -webkit-transform-style: preserve-3d;
  transition: max-height 0.2s ease-in-out;
  -webkit-transition: max-height 0.2s ease-in-out;
}
.nav-list {
  background-color: transparent;
  list-style: none;
  margin: 0;
  padding: 0;
  display: block;
  position: absolute;
  top: 4rem;
  bottom: 0;
  left: -15rem;
  width: 0;
  min-height: 100vh;
  -webkit-overflow-scrolling: touch;
  transition: all 0.3s ease-in-out;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  -webkit-backface-visibility: hidden;
  -webkit-transform-style: preserve-3d;
}
.nav-list.-open {
  width: 100%;
  left: 0;
  padding-bottom: 4rem;
}
.nav-list.-open > .list.-left {
  overflow-y: auto;
  overscroll-behavior-y: auto;
  -webkit-overflow-scrolling: touch;
}
.nav-list > .list.-left {
  background: var(--header-bg-color);
  position: relative;
  width: 60%;
  height: 100%;
}
.nav-list > .list.-right {
  width: 40%;
}
.nav-list > .list > .item {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  border-bottom: 1px solid rgba(176, 176, 176, 0.5);
