  background: #ccc091;
  width: 5px;
  height: 5px;
  border-radius: 13px;
  position: absolute;
  left: 50%;
  top: 20%;
}
.day-night-switch .onOff .sunMoon .crater.crater1 {
  left: 30%;
  top: 45%;
  transform: scale(0.5);
}
.day-night-switch .onOff .sunMoon .crater.crater2 {
  left: 55%;
  top: 60%;
  transform: scale(0.7);
}
.day-night-switch .onOff .sunMoon .cloud {
  background: white;
  border-radius: 3px;
  position: absolute;
  opacity: 0;
  transform: translatex(-20px);
  transition-delay: 0s;
}
.day-night-switch .onOff .sunMoon .cloud.part1 {
  width: 20px;
  height: 2px;
  left: -12px;
  top: calc(50% - 1.5px);
}
.day-night-switch .onOff .sunMoon .cloud.part1:before {
  content: "";
  background: white;
  border-radius: 3px;
  position: absolute;
  width: 3px;
  height: 3px;
  left: 40%;
  top: -100%;
