.day-night-switch .onOff .sunMoon .cloud.part1:after {
  content: "";
  background: white;
  border-radius: 3px;
  position: absolute;
  width: 15px;
  height: 2px;
  left: 20%;
  top: -200%;
}
.day-night-switch .onOff .sunMoon .cloud.part2 {
  width: 3px;
  height: 3px;
  left: -1px;
  top: calc(50% + 0.3px);
